/* index page */
.index-page {
    
    #chart-earning {
        height: 250px;
    }
    
    th {
        font-weight: normal;
        font-size: 1.4rem;
    }
    
    
    h4 {
        font-size: 1.4rem;
        font-weight: normal;
        .more {
            float: right;
            font-weight: normal;
        }
    }
    
    .earning-info,.am-badge {
        font-size: 1.4rem;
    }
    
}

/* sign page */
.sign-page {
    padding: 30px 0;
}

/* customer page */
.typeahead-content .twitter-typeahead {
    display: block !important;
}

/* earning page */
.earning-page {
    .am-datepicker-date {
        width: 400px;
    }
}

/* taobao-mail-create-page */
.taobao-mail-create-page {
    .copy-title {
        display: none;
    }
    .copy-info {
        display: none;
    }
    .copy-info.on {
        display: block;
    }
    .mail-title-box {
        display: none;
    }
}

/* product page */
.product-page {
    #content-main-image {
        ul {
            margin:0;
            padding: 0;
        }
        li {
            display: inline-block;
            margin-right: 5px;
            img {
                border:1px solid #ddd;
                width: 80px;
                height: 80px;
            }
        }
    }
}

/* earning page */
.earning-page {
    .earning-info,.am-badge {
        font-size: 1.4rem;
    }
}

/* order page */
.order-page {
    .am-form-inline {
        .am-form-field {
            width: 160px;
        }
    }
    .am-icon {
        &.on {
            color: #5eb95e;
        }
        &.off {
            color: #ccc;
        }
    }
}

/* purchase page */
.purchase-page {
    
    table {
        tr.off {
            background: #feffed;
        }
    }
    
    .purchase-image {
        border:1px solid #ddd;
        height: 100px;
        width: 100px;
        text-align: center;
        display: inline-block;
        line-height: 100px;
        img {
            max-width: 90px;
            max-height: 90px;
        }
    }
    
    .icon-etsy {
        font-weight: bold;
        font-style: normal;
        color: #f56400;
        font-size: 10px;
    }
}

/* identity page */
.identity-page {
    .card-view {
        img {
            width:200px;
            margin:10px 0;
            border:1px solid #ddd;
            padding: 4px;
        }
    }
}

/* product book page */
.product-book-page {
    #customer-id {
        height: 1px;
        overflow: hidden;
        display: block;
        font-size: 0;
        border:none;
    }
    .am-form-horizontal {
        margin-bottom: 2.5rem;
        margin-left:-1.5rem;
        margin-right: -1.5rem;
        .am-form-label {
            text-align: center;
            label {
                font-weight: normal;
            }
        }
        .am-form-group {
            margin-bottom: 5px;
            &:first-child {
                .remove {
                    display: none;
                }
            }
        }
        
        [class*=am-u-] {
            padding-left: 1.5rem;
            padding-right: 0rem;
        }
        
        .am-form-label {
            padding-left: 0;
            padding-right: 0;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
        
    }
    
    .am-form-inline {
        .am-form-group {
            margin-right: 10px;
        }
        .am-form-field {
            width: 160px;
        }
        label {
            height: 38px;
            line-height: 38px;
            font-weight: normal;
            margin: 0;
        }
    }
    
    .customer-info {
        color: #666;
    }
}

/* file manage page */
.file-manage-page {
    .file-type-tag {
        background: #21b2d1;
        display: inline-block;
        width: 60px;
        font-size: 14px;
        font-weight: bold;
        color: #fff;
    }
}

/* order page */
.order-page {
    .order-name-content {
        min-height: 30px;
        line-height: 30px;
    }
}